const Event = (props) => {
    const { name, url, description } = props.event;
    return (
        <a href={url} target='_blank' className='list-group-item active text-ligth mb-1'>
            <h3 className='mb-3'>{name.text}</h3>
            <p className='mb-1'>{description.text}</p>
        </a>
    );
}

export default Event;